/* animation */

.rightClicked {
	-webkit-animation: rightClickAnimation 0.3s;
	        animation: rightClickAnimation 0.3s;
}
.leftClicked {
	-webkit-animation: leftClickedAnimation 0.3s;
	        animation: leftClickedAnimation 0.3s;
}

@-webkit-keyframes rightClickAnimation {
	0% {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	}

	100% {
		-webkit-transform: rotateY(30deg);
		        transform: rotateY(30deg);
	}
}

@keyframes rightClickAnimation {
	0% {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	}

	100% {
		-webkit-transform: rotateY(30deg);
		        transform: rotateY(30deg);
	}
}
@-webkit-keyframes leftClickedAnimation {
	0% {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	}
	100% {
		-webkit-transform: rotateY(-30deg);
		        transform: rotateY(-30deg);
	}
}
@keyframes leftClickedAnimation {
	0% {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	}
	100% {
		-webkit-transform: rotateY(-30deg);
		        transform: rotateY(-30deg);
	}
}

.flex-direction-column640 {
	-webkit-flex-direction: row;
	        flex-direction: row;
}
.flex-direction-column500 {
	-webkit-flex-direction: row;
	        flex-direction: row;
}

@media (max-width: 640px) {
	.flex-direction-column640 {
		-webkit-flex-direction: column;
		        flex-direction: column;
	}
}
@media (max-width: 500px) {
	.flex-direction-column500 {
		-webkit-flex-direction: column;
		        flex-direction: column;
	}
}

